input {
  font-family: Poppins, sans-serif;
  font-weight: 400;
  line-height: 20px;

  padding: 14px 16px;
  min-height: 48px;
  box-sizing: border-box;
  width: 100%;
  border-radius: 40px;
  background: #f8f8fa;
  transition: 0.3s;
  border: 1px solid #292929;

  @include hover (border, 1px solid #D5D7DE);

&:hover::placeholder {
  color:#292929;

  // @include hover (border, 1px solid #D5D7DE);
}

